import { useSetRecoilState, useRecoilValue } from 'recoil';
import * as atoms from './atoms';
export function useGraph() {
    var graph = useRecoilValue(atoms.graph);
    return graph;
}
export function useModel() {
    var model = useRecoilValue(atoms.model);
    return model;
}
export function useSetNewGraph() {
    var setView = useSetRecoilState(atoms.view);
    var setGraph = useSetRecoilState(atoms.graph);
    var setModal = useSetRecoilState(atoms.modal);
    var setModel = useSetRecoilState(atoms.model);
    return function (graph, model) {
        setView('basemap');
        setModal(null);
        setGraph(graph);
        setModel(model);
    };
}
export function useOpenModal() {
    var setModal = useSetRecoilState(atoms.modal);
    return setModal;
}
export function useCloseModal() {
    var setModal = useSetRecoilState(atoms.modal);
    return function () { return setModal(null); };
}
